.productList {
  display: flex;
  justify-content: space-between;
  align-content: center;
  border: 1px solid #ccc;
  text-align: center;
  margin: auto;
  margin-top: 5px;
  margin-bottom: 5px;
}

.productList-image {
  width: 130px;
}

.productList-name {
  font-size: 20px;
  margin: auto;
}

.productList-rating {
  display: flex;
  margin: auto;
}

.productList-rating-stars {
  color: gold;
  margin: auto;
}

.productList-rating-number {
  margin: auto;
  margin-left: 3px;
}

.productList-reviews,
.productList-price {
  font-size: 16px;
  margin: auto;
}
.productListBtnCont {
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.productList-update-btn,
.productList-delete-btn {
  padding: 5px 10px;
  width: auto;
  height: 35px;
  font-size: 16px;
  color: #fff;
  border: none;
  cursor: pointer;
  font-weight: bold;
  border-radius: 8px;
  margin: 0 5px;
}

.productList-update-btn {
  background-color: rgb(33, 33, 173);
}

.productList-delete-btn {
  background-color: rgb(161, 23, 23);
}

.productList-update-btn:hover {
  opacity: 0.8;
}
.productList-delete-btn:hover {
  opacity: 0.8;
}

/*---------media query-----------*/
@media screen and (max-width: 992px) {
  .productList {
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    margin-top: 20px;
    width: 50%;
    padding: 15px;
    border-radius: 20px;
  }
  .productList-image {
    width: 70%;
    margin: auto;
  }
  .productListBtnCont {
    margin-top: 15px;
  }
}

@media screen and (max-width: 480px) {
  .productList {
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    margin-top: 20px;
    width: 90%;
    padding: 15px;
    border-radius: 20px;
  }
  .productList-image {
    width: 90%;
    margin: auto;
  }
  .productListBtnCont {
    margin-top: 15px;
  }
}
